import './Places.scss';
import { useEffect, useState, useContext } from 'react';
import placesPageTexts from '../../locales/places-page-RU';
import { CurrentUserContext, PopupsContext } from '../../contexts/index';
import {
  useDebounce,
  useActivityTypes,
  useLocalStorage,
} from '../../hooks/index';
import {
  COLORS,
  ALL_CATEGORIES,
  DELAY_DEBOUNCE,
  DELAY_RENDER,
  localStUserCity,
} from '../../config/constants';
import {
  handleCheckboxBehavior,
  selectOneTag,
  deselectOneTag,
  deselectAllTags,
} from '../../utils/filter-tags';
import { changeCaseOfFirstLetter } from '../../utils/utils';
import {
  BasePage,
  TitleH1,
  CardPlace,
  PlacesRecommend,
  AnimatedPageContainer,
  Loader,
  TagsList,
} from './index';
import { getPlaces, getPlacesTags } from '../../api/places-page';

function Places() {
  const {
    headTitle,
    headDescription,
    title,
    textStubNoData,
    paragraphNoContent,
    mentorTag,
    ageFilterNames,
  } = placesPageTexts;

  const ageFilters = [
    {
      filter: ageFilterNames[0].filter,
      name: ageFilterNames[0].name,
      isActive: false,
    },
    {
      filter: ageFilterNames[1].filter,
      name: ageFilterNames[1].name,
      isActive: false,
    },
    {
      filter: ageFilterNames[2].filter,
      name: ageFilterNames[2].name,
      isActive: false,
    },
    {
      filter: ageFilterNames[3].filter,
      name: ageFilterNames[3].name,
      isActive: false,
    },
  ];

  const activityTypes = useActivityTypes();

  const { currentUser } = useContext(CurrentUserContext);
  const { openPopupCities } = useContext(PopupsContext);

  const getLocalStorageItem = useLocalStorage(localStUserCity);

  // —Å–æ—Ö—Ä–∞–Ω—ë–Ω–Ω—ã–π –≤ localStorage –≥–æ—Ä–æ–¥ –∞–Ω–æ–Ω–∏–º—É—Å–∞
  const currentAnonymousCity = getLocalStorageItem();
  const userCity = currentUser?.city || currentAnonymousCity;

  // –º–µ—Å—Ç–∞ –∏–∑ API
  const [places, setPlaces] = useState(null);
  const [chosenPlace, setChosenPlace] = useState(null);

  // –ø–µ—Ä–µ—Ö–æ–¥ –º–µ–∂–¥—É —Ñ–∏–ª—å—Ç—Ä–∞–º–∏, –ª–æ–∞–¥–µ—Ä
  const [isLoading, setIsLoading] = useState(false);
  // –ø–µ—Ä–µ—Ö–æ–¥ –º–µ–∂–¥—É –≥–æ—Ä–æ–¥–∞–º–∏, –ª–æ–∞–¥–µ—Ä
  const [isCityChanging, setIsCityChanging] = useState(false);
  // —Ç—Ä–∏–≥–≥–µ—Ä —Ñ–∏–ª—å—Ç—Ä–∞ –¥–ª—è useEffect
  const [isFiltersUsed, setIsFiltersUsed] = useState(false);
  // –≤–∏–¥–Ω–∞ –ª–∏ –≥–ª–∞–≤–Ω–∞—è –∫–∞—Ä—Ç–æ—á–∫–∞
  const [isChosenCardHidden, setIsChosenCardHidden] = useState(false);
  // —Å—Ç–µ–π—Ç –¥–ª—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –ø–µ—Ä–≤–æ–π –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
  const [isFirstRender, setIsFirstRender] = useState(false);
  // –∫–∞—Ç–µ–≥–æ—Ä–∏–∏ —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏–∏
  const [ages, setAges] = useState(ageFilters); // —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∫–Ω–æ–ø–æ–∫ —Ñ–∏–ª—å—Ç—Ä–∞ –≤–æ–∑—Ä–∞—Å—Ç–∞
  const [categories, setCategories] = useState([]); // —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∫–Ω–æ–ø–æ–∫ —Ñ–∏–ª—å—Ç—Ä–∞ –∫–∞—Ç–µ–≥–æ—Ä–∏–π

  // —Ö—ç–Ω–¥–ª–µ—Ä –∫–ª–∏–∫–∞ –ø–æ —Ñ–∏–ª—å—Ç—Ä—É –ö–ê–¢–ï–ì–û–†–ò–ò
  const changeCategory = (inputValue, isChecked) => {
    if (inputValue === ALL_CATEGORIES) {
      selectOneTag(setCategories, ALL_CATEGORIES);
    } else {
      handleCheckboxBehavior(setCategories, { inputValue, isChecked });
    }
    setIsFiltersUsed(true);
  };

  // —Ö—ç–Ω–¥–ª–µ—Ä –∫–ª–∏–∫–∞ –ø–æ —Ñ–∏–ª—å—Ç—Ä—É –í–û–ó–†–ê–°–¢
  const changeAge = (inputValue, isChecked) => {
    handleCheckboxBehavior(setAges, { inputValue, isChecked });
    setIsFiltersUsed(true);
  };

  // —Ñ—É–Ω–∫—Ü–∏—è, –æ–ø—Ä–µ–¥–µ–ª—è—é—â–∞—è —Ç–µ–≥–∏ –∫–∞—Ç–µ–≥–æ—Ä–∏–π –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Ç–æ–≥–æ, –µ—Å—Ç—å –ª–∏ —Ä—É–±—Ä–∏–∫–∞ "–í—ã–±–æ—Ä –Ω–∞—Å—Ç–∞–≤–Ω–∏–∫–∞"
  const defineCategories = (tags, chosenPlaceLast) => {
    const categoriesArray = tags.map((tag) => ({
      filter: tag?.slug.toLowerCase(),
      name: changeCaseOfFirstLetter(tag?.name),
      isActive: false,
    }));
    if (chosenPlaceLast) {
      return [
        { filter: ALL_CATEGORIES, name: ALL_CATEGORIES, isActive: true },
        { filter: mentorTag, name: mentorTag, isActive: false },
        ...categoriesArray,
      ];
    }
    return [
      { filter: ALL_CATEGORIES, name: ALL_CATEGORIES, isActive: true },
      ...categoriesArray,
    ];
  };

  // —Ñ—É–Ω–∫—Ü–∏—è, –æ–ø—Ä–µ–¥–µ–ª—è—é—â–∞—è –∫–∞—Ä—Ç–æ—á–∫–∏ –ø–æ —Ñ–ª–∞–≥—É "–í—ã–±–æ—Ä –Ω–∞—Å—Ç–∞–≤–Ω–∏–∫–∞"
  // chosenPlaceLast - —Å–∞–º–∞—è "—Å–≤–µ–∂–∞—è" –∫–∞—Ä—Ç–æ—á–∫–∞ "–í—ã–±–æ—Ä –Ω–∞—Å—Ç–∞–≤–Ω–∏–∫–∞"
  // restOfPlaces - –º–∞—Å—Å–∏–≤ –±–µ–∑ —ç—Ç–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏
  const definePlaces = (placesData) => {
    const chosenPlaces = placesData.filter((place) => place?.chosen);
    const chosenPlaceLast = chosenPlaces[chosenPlaces.length - 1];
    const restOfPlaces = chosenPlaceLast
      ? placesData.filter((place) => place?.id !== chosenPlaceLast?.id)
      : placesData;
    return { chosenPlaceLast, restOfPlaces };
  };

  // —Ñ—É–Ω–∫—Ü–∏—è-—Ñ–∏–ª—å—Ç—Ä–∞—Ç–æ—Ä
  const handleFiltration = () => {
    const activeCategories = categories.filter(
      (category) => category.isActive && category.filter !== ALL_CATEGORIES
    );

    const activeTags = activeCategories
      .filter((tag) => tag.filter !== mentorTag)
      .map((tag) => tag.filter)
      .join(',');

    const activeAges = ages
      .filter((age) => age.isActive)
      .map((age) => age.filter)
      .join(',');

    const isMentorFlag = activeCategories.some(
      (tag) => tag.filter === mentorTag
    );

    // –í–°–ï
    if (activeCategories.length === 0) {
      if (activeAges.length === 0) {
        // + –ë–ï–ó –í–û–ó–†–ê–°–¢–ê (–ø–æ —É–º–æ–ª—á–∞–Ω–∏—é)
        getPlaces({ city: userCity })
          .then((placesData) => {
            const { chosenPlaceLast, restOfPlaces } = definePlaces(placesData);
            setChosenPlace(chosenPlaceLast);
            setPlaces(restOfPlaces);
            setIsChosenCardHidden(false);
          })
          .catch(console.log)
          .finally(() => setIsLoading(false));
      } else {
        // + –í–û–ó–†–ê–°–¢
        getPlaces({
          age_restriction: activeAges,
          city: userCity,
        })
          .then((placesData) => {
            setPlaces(placesData);
            setIsChosenCardHidden(true);
          })
          .catch(console.log)
          .finally(() => setIsLoading(false));
      }

      selectOneTag(setCategories, ALL_CATEGORIES);
      return;
    }

    // –ö–ê–¢–ï–ì–û–†–ò–ò + –í–û–ó–†–ê–°–¢ (–∏–ª–∏ –±–µ–∑ –Ω–µ–≥–æ)
    if (activeCategories.length > 0) {
      getPlaces({
        chosen: isMentorFlag,
        tags: activeTags,
        age_restriction: activeAges,
        city: userCity,
      })
        .then((placesData) => {
          setPlaces(placesData);
          setIsChosenCardHidden(true);
        })
        .catch(console.log)
        .finally(() => setIsLoading(false));
      deselectOneTag(setCategories, ALL_CATEGORIES);
    }
  };

  const debounceFiltration = useDebounce(handleFiltration, DELAY_DEBOUNCE);
  // –∑–∞–ø—É—Å–∫ —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏–∏
  useEffect(() => {
    if (isFiltersUsed) {
      setIsLoading(true);
      debounceFiltration();
    }
    setIsFiltersUsed(false);
    setIsFirstRender(false);
  }, [isFiltersUsed]);

  // Promise.all –Ω—É–∂–µ–Ω –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è —Ç–µ–≥–∞ "–í—ã–±–æ—Ä –Ω–∞—Å—Ç–∞–≤–Ω–∏–∫–æ–≤" –ø–æ –º–µ—Ç–∫–µ –Ω–∞ –∫–∞—Ä—Ç–æ—á–∫–∞—Ö
  useEffect(() => {
    if (userCity) {
      deselectAllTags(setAges);
      setIsFirstRender(true);
      setIsCityChanging(true);
      Promise.all([getPlaces({ city: userCity }), getPlacesTags()])
        .then(([placesData, tagsData]) => {
          const { chosenPlaceLast, restOfPlaces } = definePlaces(placesData);
          setChosenPlace(chosenPlaceLast);
          setPlaces(restOfPlaces);
          setCategories(defineCategories(tagsData, chosenPlaceLast));
          setIsChosenCardHidden(false);
        })
        .catch(console.log)
        .finally(() => setIsCityChanging(false));
    }
  }, [userCity]);

  useEffect(() => {
    if (!userCity) {
      setTimeout(() => {
        openPopupCities();
      }, DELAY_RENDER);
    }
  }, []);

  const renderPlaces = () => {
    if (places?.length > 0) {
      return (
        <>
          {chosenPlace && !isChosenCardHidden && (
            <section className="places__main fade-in">
              <CardPlace
                key={chosenPlace?.id}
                data={chosenPlace}
                activityTypes={activityTypes}
                sectionClass="card-container_type_main-article"
                isBig
              />
            </section>
          )}

          <section className="places__cards-grid">
            {places.map((place, i) => (
              <CardPlace
                key={place?.id}
                data={place}
                activityTypes={activityTypes}
                color={COLORS[i % COLORS.length]}
                sectionClass="card-container_type_article fade-in"
              />
            ))}
          </section>
        </>
      );
    }
    return <p className="places__paragraph">{paragraphNoContent}</p>;
  };

  const renderAnimatedContainer = () => (
    <>
      {!isCityChanging ? (
        <>
          <AnimatedPageContainer titleText={textStubNoData} />
          {currentUser && <PlacesRecommend activityTypes={activityTypes} />}
        </>
      ) : (
        <Loader isNested />
      )}
    </>
  );

  const renderPageContent = () => {
    if (isFirstRender && places?.length === 0) {
      return renderAnimatedContainer();
    }
    return (
      <>
        <TitleH1 title={title} sectionClass="places__title" />
        {!isCityChanging ? (
          <>
            <TagsList
              filterList={categories}
              name="categories"
              handleClick={changeCategory}
            />
            <TagsList
              filterList={ages}
              name="ages"
              handleClick={changeAge}
              sectionClass="places__tags"
            />
            {currentUser && <PlacesRecommend activityTypes={activityTypes} />}

            {!isLoading ? <>{renderPlaces()}</> : <Loader isNested />}
          </>
        ) : (
          <Loader isNested />
        )}
      </>
    );
  };

  if (!places) {
    return <Loader isCentered />;
  }

  return (
    <BasePage headTitle={headTitle} headDescription={headDescription}>
      <section className="places page__section fade-in">
        {renderPageContent()}
      </section>
    </BasePage>
  );
}

export default Places;
