import axios from 'axios';
import { apiUrl, baseURL } from '../config/config';

// –ø–æ–ª—É—á–∏—Ç—å –≥–æ—Ä–æ–¥–∞
function getCities() {
  return axios
    .get(`${baseURL}${apiUrl}/cities/`)
    .then((response) => response.data)
    .catch((err) => Promise.reject(new Error(`${err.message}`)));
}

export default getCities;
