//! –ü–ï–†–ï–î –ó–ê–õ–ò–í–û–ú –£–ë–ï–†–ò –ö–†–ê–°–ù–´–ï –†–ê–ú–ö–ò –£ –ö–ê–†–¢–û–ß–ï–ö –ö–ê–¢–ê–õ–û–ì–ê + –ò–°–ü–†–ê–í–¨ –î–û–°–¢–£–ü –ö –ö–ê–†–¢–ò–ù–ö–ï
function ReadAndWatch() {
  //! –¥–ª—è –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è –ø–æ–ø–∞–ø–∞ –≤–∏–¥–µ–æ - —é–∑–∞—Ç—å –∫–æ–Ω—Ç–µ–∫—Å—Ç –ø–æ–ø–∞–ø–æ–≤!
  const { headTitle, headDescription } = readAndWatchPageTexts;

  // –æ–ø—Ä–µ–¥–µ–ª—è–µ—Ç —Å–∫–æ–ª—å–∫–æ –æ–±—ä–µ–∫—Ç–æ–≤ –ø–æ–∫–∞–∑—ã–≤–∞—Ç—å –≤ —Ä—è–¥—É
  const [pageSize, setPageSize] = useState(null);

  //! —á—Ç–æ–±—ã —Å–¥–µ–ª–∞—Ç—å –ª–æ–∞–¥–µ—Ä –¥–µ–ª–∞–µ–º 5 —Å—Ç–µ–π—Ç–æ–≤ –∏ —Å–ø—É—Å–∫–∞–µ–º –≤ –∫–∞–∂–¥—É—é —Å–µ–∫—Ü–∏—é —Å–µ—Ç—Ç–µ—Ä —Ç–∏–ø–æ isCatalogDataLoaded, –∫–∞–∫ —Ç–æ–ª—å–∫–æ –≤—Å–µ 5 —Å—Ç–∞–Ω—É—Ç –¢–†–£ –≥–ª–æ–±–∞–ª—å–Ω—ã–π –ª–æ–∞–¥–µ—Ä –º–æ–∂–Ω–æ –≤—ã—Ä—É–±–∞—Ç—å

  useEffect(() => {
    console.log('—é–∑–ï–§–§–ï–ö–¢ -- –æ–ø—Ä–µ–¥–µ–ª–∏ —Ä–∞–∑–º–µ—Ä —ç–∫—Ä–∞–Ω–∞');
    // 1 —ç–ª–µ–º–µ–Ω—Ç –≤ —Ä—è–¥—É
    const querySizeS = window.matchMedia('(max-width: 820px)');
    // 2 —ç–ª–µ–º–µ–Ω—Ç–∞ –≤ —Ä—è–¥—É
    const querySizeM = window.matchMedia('(max-width: 1120px)');
    // 3 —ç–ª–µ–º–µ–Ω—Ç–∞ –≤ —Ä—è–¥—É
    const querySizeL = window.matchMedia('(max-width: 1440px)');
    // –±–æ–ª—å—à–µ 1440px –±—É–¥–µ—Ç 4 —ç–ª–µ–º–µ–Ω—Ç–∞ –≤ —Ä—è–¥—É

    const listener = () => {
      if (querySizeS.matches) {
        setPageSize(1);
      } else if (querySizeM.matches) {
        setPageSize(2);
      } else if (querySizeL.matches) {
        setPageSize(3);
      } else {
        setPageSize(4);
      }
    };
    listener();

    // console.log(pageSize);

    querySizeS.addEventListener('change', listener);
    querySizeM.addEventListener('change', listener);
    querySizeL.addEventListener('change', listener);
    return () => {
      querySizeS.removeEventListener('change', listener);
      querySizeM.removeEventListener('change', listener);
      querySizeL.removeEventListener('change', listener);
    };
  }, []);

  function renderPageContent() {
    if (pageSize) {
      // –°–ø—Ä–∞–≤–æ—á–Ω–∏–∫
      return (
        <>
          <ReadAndWatchSection
            pageSize={pageSize}
            getDataFromApi={getCatalogPageDat–∞}
            CardTemplateComponent={CardCatalog}
            path={CATALOG_URL}
            sectionTitle="–°–ø—Ä–∞–≤–æ—á–Ω–∏–∫"
          />
          <ReadAndWatchSection
            pageSize={pageSize}
            getDataFromApi={getVideoPageData}
            CardTemplateComponent={CardFilm}
            path={VIDEO_URL}
            sectionTitle="–í–∏–¥–µ–æ"
          />
        </>
      );
    }

    return null;
  }
  // —Å—Ç—Ä–∞–Ω–∏—Ü–∞ —Å–æ–∑–¥–∞–µ—Ç - 5 –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ —Å–µ–∫—Ü–∏–π (–æ–Ω–∏ –æ—Ç–ª–∏—á–∞—é—Ç—Å—è —Ç–æ–ª—å–∫–æ –Ω–∞–∑–≤–∞–Ω–∏—è–º–∏ –∑–∞–≥–æ–ª–æ–≤–∫–∞ –∏ —Å—Å—ã–ª–∫–æ–π, –∞ —Ç–∞–∫ –∂–µ –¥–∞—Ç–æ–π –≤–Ω—É—Ç—Ä–∏)
  return (
    <BasePage headTitle={headTitle} headDescription={headDescription}>
      <section className="readwatch-page page__section fade-in">
        {/* –°–ø—Ä–∞–≤–æ—á–Ω–∏–∫ */}
        {renderPageContent()}
        {/* –í–∏–¥–µ–æ */}
        {/* –°—Ç–∞—Ç—å–∏ */}
        {/* –§–∏–ª—å–º—ã */}
        {/* –ö–Ω–∏–≥–∏ */}
      </section>
    </BasePage>
  );
}

export default ReadAndWatch;

