import { months, weekdays } from '../config/constants';

// —Ñ–æ—Ä–º–∞—Ç–∏—Ä—É–µ—Ç —Å–µ–∫—É–Ω–¥—ã –≤ —á–∞—Å—ã, –º–∏–Ω—É—Ç—ã –∏ —Å–µ–∫—É–Ω–¥—ã, –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –Ω–∞ –≤—Ö–æ–¥ number
export const formatDuration = (duration) => {
  const hours = parseInt(duration / (60 * 60), 10);
  const minutes = parseInt((duration / 60) % 60, 10);
  const seconds = duration % 60;
  const format = (value) => (value < 10 ? `0${value}` : `${value}`);

  return {
    hours: format(hours),
    minutes: format(minutes),
    seconds: format(seconds),
  };
};

// —Ñ–æ—Ä–º–∞—Ç–∏—Ä—É–µ—Ç –¥–∞—Ç—É –≤ –æ–±—ä–µ–∫—Ç –ø–æ —Ä–∞–∑–Ω—ã–º –∫–ª—é—á–∞–º, –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –Ω–∞ –≤—Ö–æ–¥ string –≤–∏–¥–∞ "2021-05-08T21:22:00Z"
export const formatDate = (date) => {
  const parsedDate = new Date(date);
  let day = parsedDate.getDate();
  const weekdayName = weekdays[parsedDate.getDay()];
  const monthName = months[parsedDate.getMonth() + 1]; // –ø–æ—Ç–æ–º—É —á—Ç–æ months –Ω–∞—á–∏–Ω–∞–µ—Ç—Å—è —Å 1, –∞ –Ω–µ 0
  const hour = String(parsedDate.getHours());
  const year = String(parsedDate.getFullYear());
  let minutes = parsedDate.getMinutes();

  if (minutes < 10) {
    minutes = `0${minutes}`;
  }

  if (day < 10) {
    day = `0${day}`;
  }

  return {
    year,
    day,
    weekdayName,
    monthName,
    hour,
    minutes,
  };
};

// —Ñ–æ—Ä–º–∞—Ç–∏—Ä—É–µ—Ç –¥–∞—Ç—É —Ç–∏–ø–∞ "2021-05-08T21:22:00Z" –≤ 'YYYY/MM/DD'
export const parseDate = (dateString) => {
  const date = new Date(dateString);
  const year = date.getFullYear();
  let month = date.getMonth() + 1;
  let day = date.getDate();
  if (day < 10) {
    day = `0${day}`;
  }
  if (month < 10) {
    month = `0${month}`;
  }
  return `${year}-${month}-${day}`;
};

// –ø–∞–¥–µ–∂ —Å–ª–æ–≤–∞ "–º–µ—Å—Ç–æ" –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —á–∏—Å–ª–∞ –º–µ—Å—Ç
// –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –Ω–∞ –≤—Ö–æ–¥ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –æ—Å—Ç–∞–≤—à–∏—Ö—Å—è –º–µ—Å—Ç –∏–∑ —á–µ–≥–æ —Ñ–æ—Ä–º–∏—Ä—É–µ—Ç –ø–∞–¥–µ–∂ —Å–ª–æ–≤–∞
// –Ω—É–∂–Ω–æ –≤ –∫–∞—Ä—Ç–æ—á–∫–∞—Ö, –ø–æ–ø–∞–ø–∞—Ö
export const formatWordCase = (remainSeats) => {
  // –ø—Ä–æ–º–µ–∂—É—Ç–æ–∫ 11 - 14
  const lastTwoDigits = remainSeats % 100;
  if (lastTwoDigits > 10 && lastTwoDigits < 15) {
    return '–º–µ—Å—Ç';
  }
  // –µ—Å–ª–∏ –∫–æ–Ω—á–∞–µ—Ç—Å—è –Ω–∞ 1 - –æ–∫–æ–Ω—á–∞–Ω–∏–µ –û
  // –µ—Å–ª–∏ –∫–æ–Ω—á–∞–µ—Ç—Å—è –Ω–∞ 2 3 4 - –æ–∫–æ–Ω—á–∞–Ω–∏–µ A
  // –µ—Å–ª–∏ –∫–æ–Ω—á–∞–µ—Ç—Å—è –Ω–∞ –¥—Ä—É–≥–æ–µ - –æ–∫–æ–Ω—á–∞–Ω–∏—è –Ω–µ—Ç
  const lastDigit = remainSeats % 10;

  if (lastDigit === 1) {
    return '–º–µ—Å—Ç–æ';
  }
  if (lastDigit === 2 || lastDigit === 3 || lastDigit === 4) {
    return '–º–µ—Å—Ça';
  }
  return '–º–µ—Å—Ç';
};

