import React, { useEffect, useState } from 'react';
import './PopupVideo.scss';
import PropTypes from 'prop-types';
import Popup from '../Popup/Popup';
import { TitleH2, Caption } from '../../utils/index';
import parserLinkYoutube from '../../../utils/parser-link-youtube';
import Loader from '../../utils/Loader/Loader';
import { getLocalStorageData } from '../../../hooks/useLocalStorage';
import { localStChosenVideo } from '../../../config/constants';

const PopupVideo = ({ isOpen, onClose }) => {
  // –¥–æ—Å—Ç–∞—Ç—å –¥–∞–Ω–Ω—ã–µ –∏–∑ –ª–æ–∫–∞–ª–∞
  const data = getLocalStorageData(localStChosenVideo);
  const { id, title, info, link } = data;

  const [videoSrc, setVideoSrc] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  // –±–µ–∑ –∞—Å–∏–Ω–∫/–µ–≤–µ–π—Ç –º–µ–ª—å–∫–∞–µ—Ç –ø—Ä–µ–¥—ã–¥—É—â–µ–µ –≤–∏–¥–µ–æ –ø—Ä–∏ –æ—Ç–∫—Ä—ã—Ç–∏–∏ –Ω–æ–≤–æ–≥–æ
  const getSrcFrame = async () => {
    const src = await parserLinkYoutube(link);
    setVideoSrc(src);

    // –ò—Å–∫—É—Å—Ç–≤–µ–Ω–Ω–∞—è –∑–∞–¥–µ—Ä–∂–∫–∞ —á—Ç–æ –±—ã —É–≤–∏–¥–µ—Ç—å –ª–æ–∞–¥–µ—Ä
    setTimeout(() => {
      setIsLoading(false);
    }, 2 * 1000);
  };

  // –ü—Ä–∏ –æ—Ç–∫—Ä—ã—Ç–∏–∏ –Ω–æ–≤–æ–≥–æ –ø–æ–ø–∞–ø–∞ —Å –≤–∏–¥–µ–æ
  // –∑–∞–ø—É—Å–∫–∞–µ–º –∑–∞–≥—Ä—É–∫—É –∏ –ø–∞—Ä—Å–µ—Ä
  useEffect(() => {
    setIsLoading(true);
    getSrcFrame();
  }, [id]);

  return (
    <Popup
      type="video"
      typeContainer="video"
      isOpen={isOpen}
      onClose={onClose}
      withoutCloseButton
    >
      {isLoading ? (
        <Loader isNested />
      ) : (
        <>
          {isOpen && (
            <iframe
              title="youTubePlayer"
              id="playeryt"
              className="popup__video-iframe"
              src={videoSrc}
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
              seamless
            />
          )}

          <TitleH2 sectionClass="popup__video-title" title={title} />
          <Caption sectionClass="popup__video-caption" title={info} />
        </>
      )}
    </Popup>
  );
};

PopupVideo.propTypes = {
  // eslint-disable-next-line react/no-unused-prop-types
  data: PropTypes.objectOf(PropTypes.any),
  isOpen: PropTypes.bool,
  onClose: PropTypes.func,
};

PopupVideo.defaultProps = {
  data: {},
  isOpen: false,
  onClose: () => {},
};

export default PopupVideo;
